import React from 'react';
import {makeStyles, Theme, createStyles, useTheme} from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import { useDeps } from "../../context/DepsContext/DepsContext";
import AppBar from "../AppBar/AppBar";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import RSSRepositoryInterface from "../../repository/RSSRepository/RSSRepositoryInterface";
import RSSPaper from "./RSSPaper/RSSPaper";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    buttonLinkParent: {
      right: 0,
    },
    cardTitle: {
      paddingTop: theme.spacing(2),
      paddingLeft: theme.spacing(3)
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3),
      [theme.breakpoints.down('xs')]: {
        padding: 0
      }
    },
    gridItem: {
      [theme.breakpoints.down('xs')]: {
        paddingTop: theme.spacing(3),
      }
    },
    // necessary for content to be below app bar
    toolbar: {
      minHeight: 56,
      [theme.breakpoints.up('sm')]: {
        minHeight: 48,
      }
    },
  }),
);

export default function Home(props: {}) {
  const classes = useStyles();
  let theme = useTheme();
  let isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  let {
    rssRepository
  }: {
    rssRepository: RSSRepositoryInterface
  } = useDeps();

  return (
    <React.Fragment>
      <AppBar title="Home" />
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Grid container spacing={isSmall ? 0 : 3}>
          <RSSPaper
            title={"Official news"}
            source={() => rssRepository.getEveNewsFeed()}
          />
          <RSSPaper
            title={"Dev blogs"}
            source={() => rssRepository.getDevBlogsFeed()}
          />
          <RSSPaper
            title={"Patch notes"}
            source={() => rssRepository.getPatchNotesFeed()}
          />
        </Grid>
      </main>
    </React.Fragment>
  );
}
